<template>
  <div>
  </div>
</template>

<script>
  export default{
    name: 'verify',
    data() {
      return {
        status: '',
        token: atob(decodeURIComponent(this.$route.params.key))
      }
    },
    created() {
      this.verif();
      this.$emit('hilang-navbar', false)
    },
    methods: {
      async verif() {
        try {
          const resp = await this.axios.post('http://apl-test374828.heyoo.world/Verify', {exp:null}, {
            headers: {              
              authorization: `Bearer ${this.token}`
            }
          })

          this.status = resp.data.response;
          this.$swal({
            icon: this.status.status ? 'success' : 'error',
            title: 'Registrasi sukses',
            text: this.status.message
          }).then(res => {
            if(res.isConfirmed){
              window.location.href = '/';
            }
          })
        } catch(e) {
          alert(e)
        }
      }
    }
  }
</script>

